import type { AppConfig, AppProps, Touchpoint } from '@td-org/chat-widget-shared'

class Storage {
  private appConfig: AppConfig = {}
  private touchpoint: Touchpoint | null = null
  private appProps: AppProps | null = null
  private context: Record<string, string> = {}
  private participantName: string = ''

  setAppConfig(appConfig: AppConfig) {
    const styles = appConfig?.styles || {}
    this.appConfig = {
      ...this.appConfig,
      ...appConfig,
      styles: Object.keys(styles).length ? { ...this.appConfig?.styles, ...styles } : {}
    }
    return this.appConfig
  }

  setTouchpoint(touchpoint: Touchpoint) {
    this.touchpoint = touchpoint
  }

  setParticipantName(name: string) {
    this.participantName = name
  }

  setAppProps(appProps: AppProps) {
    this.appProps = appProps
  }

  getAllConfig() {
    return {
      appConfig: this.appConfig,
      touchpoint: this.touchpoint,
      context: this.context,
      appProps: this.appProps,

      participantName: this.participantName,
      injectorWindowHref: window.location.href,
      injectorWindowHostname: window.location.hostname
    }
  }

  setContextParam(data: Record<string, string>) {
    this.context = data
  }

  clearAll() {
    this.appConfig = {}
    this.touchpoint = null
    this.context = {}
    this.participantName = ''
  }
}

export default new Storage()
