import { removeEmptyKeys, type Theme } from '@td-org/chat-widget-shared'

type TriggerButtonStyles = Pick<
  Theme,
  | 'chatIcon'
  | 'chatThemeColor'
  | 'chatHoverThemeColor'
  | 'triggerButtonWidth'
  | 'triggerButtonHeight'
  | 'triggerButtonPositionBottom'
  | 'triggerButtonPositionRight'
  | 'chatPositionBottom'
  | 'chatContainerHeight'
  | 'chatContainerWidth'
  | 'chatLauncherColor'
  | 'chatHoverLauncherColor'
  | 'chatLauncherShadow'
>

export const getTriggerButtonStyles = (theme: Partial<Theme>): Partial<TriggerButtonStyles> => {
  const styles: Partial<TriggerButtonStyles> = {
    chatIcon: theme.chatIcon ? `url(${theme.chatIcon})` : undefined,
    chatThemeColor: theme.chatThemeColor,
    chatHoverThemeColor: theme.chatHoverThemeColor,
    triggerButtonWidth: theme.triggerButtonWidth,
    triggerButtonHeight: theme.triggerButtonHeight,
    triggerButtonPositionBottom: theme.triggerButtonPositionBottom,
    triggerButtonPositionRight: theme.triggerButtonPositionRight,
    chatPositionBottom: theme.chatPositionBottom,
    chatContainerHeight: theme.chatContainerHeight,
    chatContainerWidth: theme.chatContainerWidth,
    chatLauncherColor: theme.chatLauncherColor,
    chatHoverLauncherColor: theme.chatHoverLauncherColor,
    chatLauncherShadow: theme.chatLauncherShadow
  }

  return removeEmptyKeys(styles, { removeEmptyString: true })
}
