import {
  injectCssVariables,
  injectStyle,
  type PreviewConfig,
  safeJsonParse,
  type Theme,
  type Touchpoint
} from '@td-org/chat-widget-shared'

import css from './style/index.scss?inline'
import { getTriggerButtonStyles } from './utils/getTriggerButtonStyles'

const GLOBAL_STYLE_ID = 'talkdesk-chat-widget-style'

function getSettingStyles(target?: Touchpoint | PreviewConfig) {
  const touchpointConfig = (target as Touchpoint)?.config
  const configs = (
    touchpointConfig ? safeJsonParse(touchpointConfig, {}) : (target as PreviewConfig)?.widgetSettings
  ) as PreviewConfig['widgetSettings']
  const themes: Record<string, string> = {}

  if (configs?.appearance?.theme?.brandColor) {
    themes.chatThemeColor = configs?.appearance?.theme.brandColor
    themes.chatHoverThemeColor = configs?.appearance?.theme.brandColor
  }

  if (configs?.appearance?.launcher?.color) {
    themes.chatLauncherColor = configs?.appearance?.launcher?.color
    themes.chatHoverLauncherColor = configs?.appearance?.launcher?.color
  }

  if (configs?.appearance?.launcher?.iconUrl) {
    themes.chatIcon = configs?.appearance?.launcher?.iconUrl
  }

  if (configs?.appearance?.launcher?.shadow) {
    themes.chatLauncherShadow = configs.appearance.launcher.shadow
  }

  return themes
}

function getStyles(styles: Partial<Theme> = {}, target?: Touchpoint | PreviewConfig) {
  return getTriggerButtonStyles({ ...getSettingStyles(target), ...styles })
}

export function injectStyles(target: HTMLElement) {
  const cleanups: (() => void)[] = []

  cleanups.push(injectStyle(css, GLOBAL_STYLE_ID, target))

  return () => {
    cleanups.forEach((cleanup) => cleanup())
    cleanups.length = 0
  }
}

export function updateThemes<Element extends HTMLElement>(
  target: Element,
  styles: Partial<Theme> = {},
  touchpointOrPreviewConfig?: Touchpoint | PreviewConfig
) {
  const themes = getStyles(styles, touchpointOrPreviewConfig)
  const themeKeys = Object.keys(themes)
  if (themeKeys.length) {
    return injectCssVariables(target, themes)
  }
}

export function listenerScreenChange<Element extends HTMLElement>(target: Element, onChange?: () => void) {
  const update = () => {
    injectCssVariables(target, {
      chatScreenWidth: window.innerWidth + 'px',
      chatScreenHeight: window.innerHeight + 'px'
    } as Partial<Theme>)
  }

  const handlerResize = () => {
    update()
    onChange?.()
  }

  update()

  window.addEventListener('resize', handlerResize)

  return () => {
    window.removeEventListener('resize', handlerResize)
  }
}
