import { Theme } from '../types/Theme'

import { convertCssVariables } from './convertCssVariables'

export function injectCssVariables<Element extends HTMLElement>(selector: string | Element, styles: Partial<Theme>) {
  let target: HTMLElement | null

  if (typeof selector === 'string') {
    target =
      selector.indexOf('#') > -1 ? document.querySelector<HTMLElement>(selector) : document.getElementById(selector)
  } else {
    target = selector
  }

  if (target) {
    return convertCssVariables(target, styles)
  }
}
