interface AtomElement<T extends HTMLElement = HTMLElement> {
  element: T
  destroy: () => void
}

interface AtomElementActions {
  append: (key: string, element: AtomElement) => void
  remove: (key: string) => void
  show: () => void
  hide: () => void
  isMounted: () => boolean
}

export function createContainerElement(id: string): AtomElement<HTMLDivElement> & AtomElementActions {
  const element = document.createElement('div')
  element.id = id
  element.className = 'tdw-widget'

  const childElements: Map<string, AtomElement> = new Map()

  const destroy = () => {
    element.remove()
    childElements.forEach((child) => child.destroy())
    childElements.clear()
  }

  const append = (key: string, atomElement: AtomElement) => {
    childElements.set(key, atomElement)
    element.appendChild(atomElement.element)
  }

  const remove = (key: string) => {
    const childElement = childElements.get(key)
    if (childElement) {
      childElement.destroy()
      childElements.delete(key)
    }
  }

  const show = () => {
    element.setAttribute('aria-hidden', 'false')
  }

  const hide = () => {
    element.setAttribute('aria-hidden', 'true')
  }

  const isMounted = () => {
    return !!document.getElementById(id)
  }

  return {
    element,
    append,
    remove,
    show,
    hide,
    isMounted,
    destroy
  }
}

export function createLoadingElement(): AtomElement {
  const element = document.createElement('div')
  element.className = 'tdw-widget-loading'
  element.id = 'tdw-widget-loading'
  element.ariaLabel = 'Loading'
  element.innerHTML = ['<div class="tdw-widget-content">', '<span class="tdw-loader"></span>', '</div>'].join('')

  const destroy = () => {
    element.remove()
  }

  return {
    element,
    destroy
  }
}

export function createIframeElement(onLoad: () => void): AtomElement<HTMLIFrameElement> {
  const element = document.createElement('iframe')
  element.title = 'Chat Widget'
  element.allowFullscreen = true
  element.style.display = 'none'

  const onLoadHandler = () => {
    element.style.display = 'block'
    onLoad()
  }
  element.addEventListener('load', onLoadHandler)

  const destroy = () => {
    element.removeEventListener('load', onLoadHandler)
    element.remove()
  }

  return {
    element,
    destroy
  }
}
