type Options = {
  onClick?: () => void
  onChange?: (event: 'moving' | 'moveEnded') => void
}

class MoveElement<Element extends HTMLElement> {
  private _moving = false
  private _moved = false
  private _initialXY = [0, 0]
  private _startXY = [0, 0]
  private _boundaryValue = 5
  private _rb: ResizeObserver | null = null
  private _isPC = true

  constructor(
    private readonly current: Element,
    private readonly options: Options = {}
  ) {
    this.onMoveStart = this.onMoveStart.bind(this)
    this.onMoving = this.onMoving.bind(this)
    this.onMoveEnd = this.onMoveEnd.bind(this)
    this.removeDocumentListeners = this.removeDocumentListeners.bind(this)

    this.current.addEventListener('mousedown', this.onMoveStart)
    this.current.addEventListener('touchstart', this.onMoveStart)

    this._rb = new ResizeObserver(() => {
      this.reset()
    })
    this._rb.observe(document.body)
  }

  private getEventInfo(event: MouseEvent | TouchEvent) {
    if ((event as TouchEvent).touches) {
      this._isPC = false
      return {
        clientXY: [(event as TouchEvent).touches[0].clientX, (event as TouchEvent).touches[0].clientY]
      }
    } else {
      this._isPC = true
      return {
        clientXY: [(event as MouseEvent).clientX, (event as MouseEvent).clientY]
      }
    }
  }

  private onMoveStart(event: MouseEvent | TouchEvent) {
    event.stopPropagation()
    event.preventDefault()

    this._initialXY = [this.current.offsetLeft, this.current.offsetTop]
    this._startXY = this.getEventInfo(event).clientXY
    this._moved = false

    this.addDocumentListeners()
  }

  private onMoving(event: MouseEvent | TouchEvent) {
    event.stopPropagation()
    event.preventDefault()

    const { clientXY } = this.getEventInfo(event)
    const [x, y] = clientXY

    const diffX = x - this._startXY[0]
    const diffY = y - this._startXY[1]

    if (Math.abs(diffX) < this._boundaryValue && Math.abs(diffY) < this._boundaryValue) {
      return
    }

    if (!this._moved) {
      this._moved = true
    }

    this._moving = true
    this.current.style.left =
      Math.min(Math.max(0, this._initialXY[0] + diffX), window.innerWidth - this.current.offsetWidth) + 'px'
    this.current.style.top =
      Math.min(Math.max(0, this._initialXY[1] + diffY), window.innerHeight - this.current.offsetHeight) + 'px'
    this.options?.onChange?.('moving')
  }

  private onMoveEnd(event: MouseEvent | TouchEvent) {
    event.stopPropagation()
    event.preventDefault()

    if (this._moving) {
      this.options?.onChange?.('moveEnded')
    }

    if (!this._moved) {
      this.options?.onClick?.()
    }

    this._moved = false
    this.removeDocumentListeners()
  }

  private addDocumentListeners() {
    document.addEventListener('mousemove', this.onMoving, { passive: false })
    document.addEventListener('mouseup', this.onMoveEnd, { passive: false })
    this.current.addEventListener('touchmove', this.onMoving, { passive: false })
    this.current.addEventListener('touchend', this.onMoveEnd, { passive: false })
  }

  private removeDocumentListeners() {
    document.removeEventListener('mousemove', this.onMoving)
    document.removeEventListener('mouseup', this.onMoveEnd)
    this.current.removeEventListener('touchmove', this.onMoveEnd)
    this.current.removeEventListener('touchend', this.onMoveEnd)
  }

  private reset() {
    if (this._isPC) {
      this.current.style.top = ''
      this.current.style.left = ''
    }
  }

  public destroy() {
    this.current.removeEventListener('mousedown', this.onMoveStart)
    this.current.removeEventListener('touchstart', this.onMoveStart)
    this.removeDocumentListeners()
    this._rb?.disconnect()
    this._rb = null
  }
}

export { MoveElement }
