import { hasElement } from '@td-org/chat-widget-shared'

const WRAPPER_CLASSNAME = 'tdw-widget-wrapper'

type Options = {
  root: HTMLElement
  isPreview?: boolean
}

export function WidgetWrapper({ root, isPreview = false }: Options) {
  const element = document.createElement('div')

  element.className = WRAPPER_CLASSNAME
  element.setAttribute(`data-${WRAPPER_CLASSNAME}`, '')

  if (isPreview) {
    element.setAttribute('data-preview', '')
  }

  root.appendChild(element)

  return {
    element,
    destroy: () => {
      if (hasElement(root, element)) {
        root.removeChild(element)
      }
      element.remove()
    }
  }
}
