function createStyleElement(id: string): HTMLStyleElement {
  const styleElement = document.createElement('style')
  styleElement.id = id
  return styleElement
}

function getExistStyleElement(id: string, injector: HTMLElement): HTMLStyleElement | null {
  return injector.querySelector<HTMLStyleElement>(`#${id}`)
}

export function injectStyle(css: string, id: string, injector: HTMLElement = document.head): () => void {
  const existStyleElement = getExistStyleElement(id, injector)
  if (existStyleElement) {
    existStyleElement.innerHTML = css

    return () => {
      const removeStyleElement = getExistStyleElement(id, injector)
      if (removeStyleElement) {
        injector.removeChild(removeStyleElement)
      }
    }
  } else {
    const styleElement = createStyleElement(id)
    styleElement.innerHTML = css
    injector.appendChild(styleElement)

    return () => {
      const removeStyleElement = getExistStyleElement(id, injector)
      if (removeStyleElement) {
        injector.removeChild(removeStyleElement)
      }
    }
  }
}
