function isUpperCase(char: string): boolean {
  const code = char.charCodeAt(0)
  return code >= 65 && code <= 90
}

function convertKey(key: string): string {
  const result = [...key]
    .map((c, index) => {
      if (isUpperCase(c)) {
        return index === 0 ? c.toLowerCase() : '-' + c.toLowerCase()
      } else {
        return c
      }
    })
    .join('')

  return `--${result}`
}
export function convertCssVariables<Element extends HTMLElement>(
  target: Element,
  themes: Record<string, string>
): Record<string, string> {
  const themeKeys = Object.keys(themes)

  if (themeKeys.length) {
    return themeKeys.reduce(
      (acc, key) => {
        if (key.indexOf('-') > -1 || key.indexOf('_') > -1) {
          return acc
        } else {
          target.style.setProperty(convertKey(key), themes[key])
          return { ...acc, [convertKey(key)]: themes[key] }
        }
      },
      {} as Record<string, string>
    )
  }

  return {} as Record<string, string>
}
