import { MoveElement } from '@td-org/chat-widget-shared'

import WidgetTriggerAnimate from './WidgetTriggerAnimate.ts'

type UpdatePayload = {
  enablePointMoveTriggerButton?: boolean
  themes?: Record<string, string>
}

export type IWidgetTrigger = {
  open: () => void
  close: () => void
  update: (payload: UpdatePayload) => void
  destroy: () => void
  setBadge: (count: number) => void
}

const IDENTITY = 'talkdesk-chat-widget-trigger'

export function WidgetTrigger(injector: HTMLElement, onClick?: () => void, isPreview = false): IWidgetTrigger {
  const widgetTriggerAnimate = WidgetTriggerAnimate()
  const element = document.createElement('button')
  element.id = IDENTITY
  element.className = 'tdw-widget-trigger'
  element.ariaLabel = 'Open chat widget'

  if (isPreview) {
    element.setAttribute('data-preview', '')
  }

  let badgeElement: HTMLSpanElement | null = null
  let moveElementInstance: MoveElement<HTMLButtonElement> | null = null

  const inactivePointMove = () => {
    moveElementInstance?.destroy()
    moveElementInstance = null
  }

  const activePointMove = () => {
    if (!moveElementInstance) {
      moveElementInstance = new MoveElement<HTMLButtonElement>(element, {
        onClick: () => {
          onClick?.()
        }
      })
    }
  }

  const init = () => {
    if (!document.getElementById(IDENTITY)) {
      injector.appendChild(element)
      element.appendChild(widgetTriggerAnimate.element)
      element.onclick = () => {
        if (!moveElementInstance) {
          onClick?.()
        }
      }
    }
  }

  const open = () => {
    init()
    element.style.display = ''
  }

  const close = () => {
    element.style.display = 'none'
  }

  const update = ({ enablePointMoveTriggerButton, themes = {} }: UpdatePayload) => {
    if (enablePointMoveTriggerButton) {
      activePointMove()
    }
    element.setAttribute('data-has-chat-icon', themes?.['--chat-icon'] ? 'yes' : 'no')
  }

  const destroy = () => {
    inactivePointMove()

    element.removeChild(widgetTriggerAnimate.element)
    widgetTriggerAnimate.destroy()

    element.remove()
  }

  const setBadge = (count: number) => {
    if (count > 0) {
      if (!badgeElement) {
        badgeElement = document.createElement('span')
        badgeElement.className = `tdw-widget-trigger-badge ${count > 9 ? 'tdw-widget-trigger-badge-more' : ''}`
        badgeElement.ariaLabel = 'new message indicator'
        element.appendChild(badgeElement)
      }
      badgeElement.textContent = count > 99 ? '99+' : count.toString()
    } else {
      if (badgeElement) {
        badgeElement.remove()
        badgeElement = null
      }
    }
  }

  return {
    open,
    close,
    update,
    destroy,
    setBadge
  }
}
