import { PROXY_URI } from '@td-org/chat-widget-shared'

import storage from '../utils/storage.ts'
import validVersion from '../utils/validVersion.ts'

import { createContainerElement, createIframeElement, createLoadingElement } from './elements.ts'

const IDENTITY = 'talkdesk-chat-widget'
const LOADING_ELEMENT_KEY = 'loading-element'
const IFRAME_ELEMENT_KEY = 'iframe-element'

type UpdatePayload = {
  enableResponsiveLayout?: boolean
}

type ResizePayload = {
  height?: number | string
}

export type IWidgetIFrame = {
  initInBackground: () => void
  open: () => void
  close: () => void
  destroy: () => void
  update: (payload: UpdatePayload) => void
  resize: (payload: ResizePayload) => void
}

const getSrc = () => {
  const { appProps } = storage.getAllConfig()
  if (import.meta.env.DEV) {
    return `${import.meta.env.VITE_CDN_BASE}?touchpointId=${appProps?.touchpointId}`
  }
  return `./v2/widget/latest/index.html?touchpointId=${appProps?.touchpointId}`
  // const releaseVersion = touchpoint?.release_version || ''
  // const validated = validVersion(releaseVersion)
  // return `${proxyUrl || import.meta.env.VITE_CDN_BASE}/v2/widget/${
  //   validated ? releaseVersion : 'latest'
  // }/index.html?touchpointId=${appProps?.touchpointId}`
}

const getPreviewSrc = () => {
  if (import.meta.env.DEV) {
    return import.meta.env.VITE_CDN_BASE + '/preview.html'
  }
  return `${import.meta.env.VITE_CDN_BASE}/v2/widget/latest/preview.html`
}

export function WidgetFrame(
  injector: HTMLElement,
  onAppend?: (window: Window) => void,
  isPreview = false
): IWidgetIFrame {
  const container = createContainerElement(IDENTITY)
  const iframe = createIframeElement(() => {
    container.remove(LOADING_ELEMENT_KEY)
  })
  container.append(LOADING_ELEMENT_KEY, createLoadingElement())
  container.hide()

  if (isPreview) {
    container.element.setAttribute('data-preview', '')
  }

  const init = () => {
    if (!container.isMounted()) {
      injector.appendChild(container.element)
      iframe.element.src = isPreview ? getPreviewSrc() : getSrc()
      container.append(IFRAME_ELEMENT_KEY, iframe)
      if (onAppend && iframe.element.contentWindow) {
        onAppend(iframe.element.contentWindow)
      }
    }
  }

  const initInBackground = () => {
    init()
  }

  const resize = ({ height }: ResizePayload) => {
    if (!height) return

    container.element.style.height = typeof height === 'string' ? height : `${height}px`
  }

  const open = () => {
    init()
    container.show()
  }

  const close = () => {
    container.hide()
  }

  const destroy = () => {
    container.destroy()
  }

  const update = ({ enableResponsiveLayout = false }: UpdatePayload = {}) => {
    if (enableResponsiveLayout) {
      container.element.setAttribute('data-responsive-layout', 'true')
    } else {
      container.element.removeAttribute('data-responsive-layout')
    }
  }

  return {
    initInBackground,
    resize,
    open,
    close,
    destroy,
    update
  }
}
