import { type AppConfig, type PreviewConfig, RpcConstants, safeJsonToString } from '@td-org/chat-widget-shared'
import RPC from '@td-org/digital-postmessage-rpc'

import storage from './utils/storage'
import { injectStyles, listenerScreenChange, updateThemes } from './styles'
import { WidgetFrame, WidgetTrigger, WidgetWrapper } from './WidgetElement'

type Callback = () => void

export default function Preview(injector: string) {
  const root = document.getElementById(injector)
  if (!root) {
    throw new Error(`The element not exist, element id = "#${injector}"`)
  }

  const cleanups: Callback[] = []

  let rpc: RPC | null = null
  let previewReady = false
  let _previewConfig: PreviewConfig | null = null
  let unListenerScreenChange: (() => void) | null = null

  const widgetWrapper = WidgetWrapper({ root, isPreview: true })
  const widgetTrigger = WidgetTrigger(widgetWrapper.element, () => {}, true)
  const widgetFrame = WidgetFrame(
    widgetWrapper.element,
    (window) => {
      rpc = new RPC({
        name: 'SDK',
        sender: window,
        topic: RpcConstants.Topic,
        debug: import.meta.env.MODE === 'development'
      })

      rpc.register(RpcConstants.Methods.GetAllConfig, () => {
        const config = storage.getAllConfig()
        return config
      })

      rpc.register(RpcConstants.Methods.PreviewReady, () => {
        previewReady = true
        rpc?.trigger(RpcConstants.Methods.UpdatePreviewConfig, _previewConfig)
      })
    },
    true
  )

  const init = (appConfig: AppConfig = {}) => {
    storage.setAppConfig(appConfig)
    cleanups.push(injectStyles(widgetWrapper.element))
    if (!unListenerScreenChange) {
      unListenerScreenChange = listenerScreenChange(widgetWrapper.element)
    }
    widgetFrame.open()
    widgetTrigger.open()
  }

  const destroy: () => void = () => {
    widgetTrigger.destroy()
    widgetFrame.destroy()
    widgetWrapper.destroy()
    cleanups.forEach((cleanup) => cleanup())
    cleanups.length = 0
    _previewConfig = null
    storage.clearAll()
    unListenerScreenChange?.()
    unListenerScreenChange = null
  }

  const updateConfig = (previewConfig: PreviewConfig) => {
    storage.setTouchpoint({ active: true, config: safeJsonToString(previewConfig?.widgetSettings), account_id: '' })
    storage.setParticipantName(previewConfig.userName || '')

    const themes = updateThemes(widgetWrapper.element, storage.getAllConfig()?.appConfig?.styles, previewConfig)
    widgetTrigger.update({ themes })

    if (previewReady) {
      rpc?.trigger(RpcConstants.Methods.UpdatePreviewConfig, previewConfig)
    } else {
      _previewConfig = previewConfig
    }
  }

  return {
    init,
    destroy,
    updateConfig
  }
}
