export function safeJsonToString<T>(target: T, onError?: (e: unknown) => void) {
  try {
    return JSON.stringify(target)
  } catch (e) {
    onError?.(e)
    return ''
  }
}

export function safeJsonParse<T>(target: string, defaultValue: T, onError?: (e: unknown) => void) {
  try {
    return JSON.parse(target) as T
  } catch (e) {
    onError?.(e)
    return defaultValue
  }
}
